<template>
  <div>
    <Navbar class="bg-primary"/>
    <Sidebar/>
    <section class="bg-gray-200 font-sans text-primary py-32 mt-20">
      <div class="container">
        <div class="flex justify-between items-center mb-10">
          <div style="font-family: initial;" class="text-4xl">DIRECTORY</div>
          <div class="flex">
            <input type="text" placeholder="Search name" v-model="search" @input="searchData">
            <div class="input-icon">
              <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" aria-hidden="true" focusable="false" width="25px" preserveAspectRatio="xMidYMid meet" viewBox="0 0 24 24">
                <g fill="none" stroke="#B18A54" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
                  <circle cx="11" cy="11" r="8"/><path d="M21 21l-4.35-4.35"/>
                </g>
                <rect x="0" y="0" width="24" height="24" fill="rgba(0, 0, 0, 0)" />
              </svg>
            </div>
          </div>
        </div>
        <table class="table-auto w-full mb-32">
          <tbody>
            <tr v-if="result.length == 0">
              <td colspan="4">
                <div class="p-4 bg-primary text-white">
                  Sorry, name not found
                </div>
              </td>
            </tr>
            <tr v-else v-for="item in result" :key="item.id">
              <td>
                <div class="text-xl uppercase">{{ item.name }}</div>
                <div class="capitalize">{{ item.job_detail }}</div>
              </td>
              <td>
                <div class="flex items-center">
                  <div class="icon-circle">
                    <img src="@/assets/icon/wa.svg" alt="icon" width="25">
                  </div>
                  <span class="ml-3">{{ item.wa }}</span>
                </div>
              </td>
              <td>
                <div class="flex items-center">
                  <div class="icon-circle">
                    <img src="@/assets/icon/phone.svg" alt="icon" width="25">
                  </div>
                  <span class="ml-3">{{ item.telepon }}</span>
                </div>
              </td>
              <td>
                <div class="flex items-center">
                  <div class="icon-circle">
                    <img src="@/assets/icon/mail.svg" alt="icon" width="25">
                  </div>
                  <span class="ml-3">{{ item.email }}</span>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
        <Subscribe/>
      </div>
    </section>
    <Footer/>
  </div>
</template>

<script>
import Navbar from '@/components/Navbar'
import Sidebar from '@/components/Sidebar'
import Footer from '@/components/Footer'
import Subscribe from '@/components/Subscribe'
import http from '@/config/http'

export default {
  components: {
    Subscribe,
    Footer,
    Sidebar,
    Navbar
  },
  data: () => ({
    data: [],
    result: [],
    search: ''
  }),
  methods: {
    getData: async function () {
      http.get('directory')
        .then(res => {
          this.data = res.data.data
          this.result = res.data.data
        })
    },
    searchData(){
      const result = this.data.filter(x => x.name.includes(this.search))
      this.result = result
    }
  },
  created() {
    this.getData()
  }
}
</script>

<style scoped>
input{
  background-color: transparent;
  @apply appearance-none px-3 py-2 border-b border-gray-500;
}
input:focus{
  @apply outline-none;
}
.input-icon{
  width: 40px;
  height: 41px;
  @apply flex justify-center items-center border-b border-gray-500;
}
.icon-circle{
  width: 40px;
  height: 40px;
  border-color: #B18A54;
  @apply rounded-full border-2 flex items-center justify-center duration-100;
}
.icon-circle:hover{
  border-color: #ffffff;
  background-color: #ffffff;
}
</style>